        <view class="login-btn" @click="handleLogin">
          <text>{{ userInfo.nickName || 'ÁÇπÂáªÁôªÂΩï' }}</text>
        </view>
      </view>
      <view class="user-stats">
        <view class="stat-item">
          <view class="stat-number">{{ totalLearnedWords }}</view>
          <view class="stat-label">ÊÄªÂ≠¶‰π†ËØçÊ±á</view>
        </view>
        <view class="stat-item">
          <view class="stat-number">{{ totalStudyDays }}</view>
          <view class="stat-label">Â≠¶‰π†Â§©Êï∞</view>
        </view>
        <view class="stat-item">
          <view class="stat-number">{{ totalWrongWords }}</view>
          <view class="stat-label">ÊÄªÈîôÈ¢òÊï∞</view>
        </view>
      </view>
    </view>

    <!-- Â≠¶‰π†ÊàêÂ∞± -->
    <view class="achievements-section">
      <view class="section-title">
        <text>üèÜ Â≠¶‰π†ÊàêÂ∞±</text>
      </view>
      <view class="achievements-grid">
        <view 
          v-for="achievement in achievements" 
          :key="achievement.id"
          class="achievement-card"
          :class="{ 'unlocked': achievement.unlocked }"
        >
          <view class="achievement-icon">{{ achievement.icon }}</view>
          <view class="achievement-info">
            <view class="achievement-title">{{ achievement.title }}</view>
            <view class="achievement-desc">{{ achievement.description }}</view>
          </view>
          <view v-if="achievement.unlocked" class="achievement-badge">‚úì</view>
        </view>
      </view>
    </view>

    <!-- ‰π¶Á±çÂ≠¶‰π†ËøõÂ∫¶ -->
    <view class="books-progress-section">
      <view class="section-title">
        <text>üìö ‰π¶Á±çËøõÂ∫¶</text>
      </view>
      <view class="books-progress-list">
        <view 
          v-for="book in bookList" 
          :key="book.id"
          class="book-progress-item"
        >
          <view class="book-icon">{{ book.cover }}</view>
          <view class="book-info">
            <view class="book-title">{{ book.title }}</view>
            <view class="progress-bar">
              <view 
                class="progress-fill"
                :style="{ width: getBookProgressPercent(book.id) + '%', backgroundColor: book.color }"
              ></view>
            </view>
            <view class="progress-text">{{ getBookProgressPercent(book.id) }}%</view>
          </view>
        </view>
      </view>
    </view>

    <!-- ËÆæÁΩÆÂíåÂäüËÉΩ -->
    <view class="settings-section">
      <view class="section-title">
        <text>‚öôÔ∏è ËÆæÁΩÆ</text>
      </view>
      <view class="settings-list">
        <view class="setting-item" @click="showStudyStats">
          <view class="setting-icon">üìä</view>
          <view class="setting-text">Â≠¶‰π†ÁªüËÆ°</view>
          <view class="setting-arrow">></view>
        </view>
        <view class="setting-item" @click="exportAllData">
          <view class="setting-icon">üì§</view>
          <view class="setting-text">ÂØºÂá∫Êï∞ÊçÆ</view>
          <view class="setting-arrow">></view>
        </view>
        <view class="setting-item" @click="importData">
          <view class="setting-icon">üì•</view>
          <view class="setting-text">ÂØºÂÖ•Êï∞ÊçÆ</view>
          <view class="setting-arrow">></view>
        </view>
        <view class="setting-item" @click="resetAllData">
          <view class="setting-icon">üîÑ</view>
          <view class="setting-text">ÈáçÁΩÆÊâÄÊúâÊï∞ÊçÆ</view>
          <view class="setting-arrow">></view>
        </view>
        <view class="setting-item" @click="showAbout">
          <view class="setting-icon">‚ÑπÔ∏è</view>
          <view class="setting-text">ÂÖ≥‰∫éÂ∫îÁî®</view>
          <view class="setting-arrow">></view>
        </view>
      </view>
    </view>
  </view>
</template>

<script>
import { 
  bookList, 
  getBookProgress,
  saveBookProgress
} from '@/utils/bookData.js'

export default {
  data() {
    return {
      bookList,
      userInfo: {},
      totalLearnedWords: 0,
      totalStudyDays: 1,
      totalWrongWords: 0,
      achievements: [
        {
          id: 1,
          title: 'ÂàùÂ≠¶ËÄÖ',
          description: 'Â≠¶‰π†Á¨¨‰∏Ä‰∏™ÂçïËØç',
          icon: 'üå±',
          unlocked: false,
          condition: (stats) => stats.totalLearnedWords >= 1
        },
        {
          id: 2,
          title: 'Âã§Â≠¶ËÄÖ',
          description: 'Â≠¶‰π†50‰∏™ÂçïËØç',
          icon: 'üìö',
          unlocked: false,
          condition: (stats) => stats.totalLearnedWords >= 50
        },
        {
          id: 3,
          title: 'ËøõÊ≠•ËÄÖ',
          description: 'Â≠¶‰π†100‰∏™ÂçïËØç',
          icon: 'üöÄ',
          unlocked: false,
          condition: (stats) => stats.totalLearnedWords >= 100
        },
        {
          id: 4,
          title: '‰∏ìÂÆ∂Á∫ß',
          description: 'Â≠¶‰π†200‰∏™ÂçïËØç',
          icon: 'üëë',
          unlocked: false,
          condition: (stats) => stats.totalLearnedWords >= 200
        },
        {
          id: 5,
          title: 'ÂÆåÁæé‰∏ª‰πâËÄÖ',
          description: 'ÂÆåÊàê‰∏ÄÊú¨‰π¶ÁöÑÂ≠¶‰π†',
          icon: 'üíØ',
          unlocked: false,
          condition: (stats) => stats.completedBooks >= 1
        },
        {
          id: 6,
          title: 'ÂùöÊåÅËÄÖ',
          description: 'ËøûÁª≠Â≠¶‰π†7Â§©',
          icon: 'üî•',
          unlocked: false,
          condition: (stats) => stats.totalStudyDays >= 7
        }
      ]
    }
  },
  onLoad() {
    this.loadUserData()
  },
  onShow() {
    this.loadUserData()
  },
  methods: {
    loadUserData() {
      // Âä†ËΩΩÁî®Êà∑‰ø°ÊÅØ
      try {
        this.userInfo = uni.getStorageSync('userInfo') || {}
      } catch (e) {
        this.userInfo = {}
      }
      
      // ËÆ°ÁÆóÊÄªÂ≠¶‰π†ÁªüËÆ°
      this.calculateTotalStats()
      
      // Êõ¥Êñ∞ÊàêÂ∞±Áä∂ÊÄÅ
      this.updateAchievements()
    },
    
    calculateTotalStats() {
      let totalLearned = 0
      let totalWrong = 0
      let completedBooks = 0
      
      bookList.forEach(book => {
        const progress = getBookProgress(book.id)
        totalLearned += progress.learnedWords.length
        totalWrong += progress.wrongWords.length
        
        // Ê£ÄÊü•ÊòØÂê¶ÂÆåÊàê‰∫ÜËøôÊú¨‰π¶
        if (progress.learnedWords.length >= book.wordCount) {
          completedBooks++
        }
      })
      
      this.totalLearnedWords = totalLearned
      this.totalWrongWords = totalWrong
      this.completedBooks = completedBooks
      
      // ËÆ°ÁÆóÂ≠¶‰π†Â§©Êï∞ÔºàÁÆÄÂåñÁâàÊú¨Ôºâ
      try {
        const firstStudyDate = uni.getStorageSync('firstStudyDate')
        if (firstStudyDate) {
          const now = new Date()
          const firstDate = new Date(firstStudyDate)
          const diffTime = Math.abs(now - firstDate)
          this.totalStudyDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24))
        } else {
          // Â¶ÇÊûúÊòØÁ¨¨‰∏ÄÊ¨°‰ΩøÁî®ÔºåËÆ∞ÂΩïÂºÄÂßãÊó∂Èó¥
          uni.setStorageSync('firstStudyDate', new Date().toISOString())
          this.totalStudyDays = 1
        }
      } catch (e) {
        this.totalStudyDays = 1
      }
    },
    
    updateAchievements() {
      const stats = {
        totalLearnedWords: this.totalLearnedWords,
        totalStudyDays: this.totalStudyDays,
        completedBooks: this.completedBooks
      }
      
      this.achievements.forEach(achievement => {
        achievement.unlocked = achievement.condition(stats)
      })
    },
    
    getBookProgressPercent(bookId) {
      const progress = getBookProgress(bookId)
      const book = bookList.find(b => b.id === bookId)
      if (!book) return 0
      return Math.round((progress.learnedWords.length / book.wordCount) * 100)
    },
    
    handleLogin() {
      // ÂæÆ‰ø°ÁôªÂΩïÂäüËÉΩ
      uni.getUserProfile({
        desc: 'Áî®‰∫éÂÆåÂñÑ‰ºöÂëòËµÑÊñô',
        success: (res) => {
          this.userInfo = res.userInfo
          uni.setStorageSync('userInfo', res.userInfo)
          uni.showToast({
            title: 'ÁôªÂΩïÊàêÂäü',
            icon: 'success'
          })
        },
        fail: () => {
          uni.showToast({
            title: 'ÁôªÂΩïÂ§±Ë¥•',
            icon: 'none'
          })
        }
      })
    },
    
    showStudyStats() {
      let statsText = 'üìä ËØ¶ÁªÜÂ≠¶‰π†ÁªüËÆ°\n\n'
      statsText += `ÊÄªÂ≠¶‰π†ËØçÊ±áÔºö${this.totalLearnedWords}‰∏™\n`
      statsText += `Â≠¶‰π†Â§©Êï∞Ôºö${this.totalStudyDays}Â§©\n`
      statsText += `ÊÄªÈîôÈ¢òÊï∞Ôºö${this.totalWrongWords}‰∏™\n`
      statsText += `ÂÆåÊàê‰π¶Á±çÔºö${this.completedBooks}Êú¨\n\n`
      
      bookList.forEach(book => {
        const progress = getBookProgress(book.id)
        const percent = this.getBookProgressPercent(book.id)
        statsText += `${book.title}Ôºö${percent}%\n`
      })
      
      uni.showModal({
        title: 'Â≠¶‰π†ÁªüËÆ°',
        content: statsText,
        showCancel: false
      })
    },
    
    exportAllData() {
      const exportData = {
        exportTime: new Date().toLocaleString(),
        userInfo: this.userInfo,
        stats: {
          totalLearnedWords: this.totalLearnedWords,
          totalStudyDays: this.totalStudyDays,
          totalWrongWords: this.totalWrongWords
        },
        books: {}
      }
      
      bookList.forEach(book => {
        exportData.books[book.id] = {
          title: book.title,
          progress: getBookProgress(book.id),
          progressPercent: this.getBookProgressPercent(book.id)
        }
      })
      
      uni.showModal({
        title: 'ÂØºÂá∫Êï∞ÊçÆ',
        content: 'Â≠¶‰π†Êï∞ÊçÆÂ∑≤ÁîüÊàêÔºåËØ∑Âú®ÊéßÂà∂Âè∞Êü•ÁúãÂπ∂Â§çÂà∂‰øùÂ≠ò„ÄÇ',
        showCancel: false,
        success: () => {
          console.log('Export data:', JSON.stringify(exportData, null, 2))
        }
      })
    },
    
    importData() {
      uni.showModal({
        title: 'ÂØºÂÖ•Êï∞ÊçÆ',
        content: 'Ê≠§ÂäüËÉΩÈúÄË¶ÅÂÖàÂØºÂá∫Êï∞ÊçÆÔºåÁÑ∂ÂêéÈÄöËøáÂºÄÂèëËÄÖÂ∑•ÂÖ∑ÂØºÂÖ•„ÄÇ',
        showCancel: false
      })
    },
    
    resetAllData() {
      uni.showModal({
        title: 'ÈáçÁΩÆÊâÄÊúâÊï∞ÊçÆ',
        content: 'Á°ÆÂÆöË¶ÅÈáçÁΩÆÊâÄÊúâÂ≠¶‰π†Êï∞ÊçÆÂêóÔºüÊ≠§Êìç‰Ωú‰∏çÂèØÊÅ¢Â§çÔºÅ',
        success: (res) => {
          if (res.confirm) {
            // ÈáçÁΩÆÊâÄÊúâ‰π¶Á±çËøõÂ∫¶
            bookList.forEach(book => {
              const emptyProgress = {
                learnedWords: [],
                wrongWords: [],
                reviewWords: []
              }
              saveBookProgress(book.id, emptyProgress)
            })
            
            // ÈáçÁΩÆÁî®Êà∑Êï∞ÊçÆ
            uni.removeStorageSync('userInfo')
            uni.removeStorageSync('firstStudyDate')
            
            // ÈáçÊñ∞Âä†ËΩΩÊï∞ÊçÆ
            this.loadUserData()
            
            uni.showToast({
              title: 'ÈáçÁΩÆÊàêÂäü',
              icon: 'success'
            })
          }
        }
      })
    },
    
    showAbout() {
      uni.showModal({
        title: 'ÂÖ≥‰∫éËÆ∞ÊÑè Parli',
        content: 'ËÆ∞ÊÑè Parli v1.0\n\n‰∏Ä‰∏™ÁÆÄÊ¥ÅÈ´òÊïàÁöÑÊÑèÂ§ßÂà©ËØ≠ÂçïËØçÂ≠¶‰π†Â∫îÁî®\n\nÂäüËÉΩÁâπËâ≤Ôºö\n‚Ä¢ Â§ö‰π¶Á±çÂ≠¶‰π†Á≥ªÁªü\n‚Ä¢ Êô∫ËÉΩÈîôÈ¢òÂ§ç‰π†\n‚Ä¢ Â≠¶‰π†ËøõÂ∫¶ËøΩË∏™\n‚Ä¢ ÈöèÊú∫ÊäΩÊü•ÊµãËØï\n\nËÆ©ÊÑèÂ§ßÂà©ËØ≠Â≠¶‰π†ÂèòÂæóÊõ¥ÁÆÄÂçïÔºÅ',
        showCancel: false
      })
    }
  }
}
</script>

